/* App.css */

.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  background-color: #282c34;
}

.App-header {
  background-color: #485267;
  color: white;
  padding: 20px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px; /* Adds space between elements */
}

/* Container for buttons to align them side by side */
.button-container {
  display: flex;
  gap: 20px; /* Adjust the space between buttons as needed */
}

/* Styles for the 'Nouvelle traduction' button */
.new-translation-button {
  background-color: #4CAF50; /* Green */
  color: white;
}

/* Styles for the 'Afficher tout/Masquer' button */
.toggle-display-button {
  background-color: #008CBA; /* Blue */
  color: white;
}

button {
  padding: 10px 20px;
  font-size: 1.2rem;
  cursor: pointer;
  border: none;
  border-radius: 5px;
}

table {
  margin-top: 20px;
  border-collapse: collapse;
  width: 80%; /* Adjust based on your preference */
}

th, td {
  border: 1px solid #ddd;
  text-align: left;
  padding: 8px;
}

th {
  background-color: #61dafb;
  color: white;
}

/* Responsive table */
@media screen and (max-width: 768px) {
  table {
    width: 100%;
  }
}
